@import "../../../components/miyagi-core/styles/variables";
@import "../../../components/miyagi-core/styles/mixins";

.accordion {
    display: block;
}

.accordion__group {
    margin-top: 5.2rem;

    &:first-of-type {
        margin-top: 0;
    }
}

.accordion__group__title {
    font-size: 2rem;
    line-height: 2.8rem;
    @extend %primary-font-normal;
    color: #4a4a4a;

    @include bp(tab-up) {
        font-size: 2.4rem;
        margin-bottom: 1.8rem;
    }
}

.accordion__item {
    border-bottom: 1px solid map-get($color, divider);
}

.accordion__header {
    cursor: pointer;
    margin: 0;
    position: relative;
    padding: 0 20px;
    /*padding: 0 map-get($keyline, small);*/

    @include bp(tab-up) {
        /*padding: 0 map-get($keyline, large);*/
    }

    .accordion__item--is-active & {

        .accordion__icon {
            transform: rotate(90deg);
        }
    }
}

.accordion__title {
    font-size: 2rem;
    @extend %primary-font-normal;
    line-height: 2.2rem;
    color: rgba(0,0,0,0.87);
    padding: 3rem 0;
    line-height: 2.4rem;
    width: 96%;
    cursor: pointer;

    @include bp(tab-up) {
        font-size: 2rem;
        padding: 2.3rem 0;
    }
}

.accordion__icon {
    transition: transform .1s;
    margin: -0.9rem 0 0;
    width: 1.8rem;
    height: 1.8rem;
    pointer-events: none;
    position: absolute;
    top: 50%;
    right: map-get($keyline, small);
    color: map-get($color, action-colored);
    font-size: 2rem;

    @include bp(tab-up) {
        right: map-get($keyline, large);
    }
}

.accordion__panel {
    display: none;
    overflow: hidden;
    transition: height 400ms;
    /*padding: 0 map-get($keyline, small);*/
    padding: 0 2rem;

    @include bp(tab-up) {
        /*padding: 0 map-get($keyline, large);*/
        padding: 0 2rem;
    }

    .accordion__item--is-active & {
        display: block;
    }

    > p {
        margin: 0;
        padding-bottom: 2rem;
        color: map-get($black, secondary);

        &:last-of-type {
            padding-bottom: 4.1rem;

            @include bp(tab-up) {
                padding-bottom: 3.5rem;
            }
        }
    }
}



// Schedule Accordian Specific Styling.

.schedule-accordian {}

.schedule-accordian__item  {}


// Schedule Accordian Item Header.

.schedule-accordian__header {
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    align-self: stretch;
    flex: 1 1 auto;
    min-height: 12rem;
    padding: map-get($keyline, small);

    @include bp(tab-up) {
        padding: 0 map-get($keyline, large);
        padding-right: 10.5rem;
        min-height: 14.5rem;
    }

    .accordion__item--is-active & {
        .accordion__icon {
            transform: rotate(180deg);
        }
    }
}

.schedule-accordian__header--inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: stretch;
    width: 100%;
    min-height: 12rem;

    @include bp(tab-up) {
        flex-direction: row;
        align-content: center;
        align-items: center;
        justify-content: space-between;
        flex: 1 1 auto;
        min-height: 14.5rem;
    }
}

.schedule-accordion__title.accordion__title {
    @extend %primary-font-light;
    color: map-get($black, text);
    font-size: 2.4rem;
    line-height: 2.8rem;
    padding:0;
    padding-right: map-get($keyline, large);
    max-width: 42.4rem;
}

.schedule-accordian__details {
    margin-top: 0.7rem;
    display: flex;
    flex-direction: row;
    align-self: stretch;

    @include bp(tab-up) {
        margin: 0;
        flex-shrink: 0;
        align-self: center;
    }
}

.schedule-accordion__icon {
    width: 1em;
    height: 1em;
    margin: -1.2rem 0 0;
}

// Schedule Accordian Item Panel.

.schedule-accordian__panel {
}

.schedule-accordion__panel__desc {
    @extend %primary-font-light;
    color: #000000;
    max-width: 57.4rem;
}

.details-zone_ctn {
    display: flex;
    flex-direction: column;
    align-self: stretch;

    @include bp(tab-up) {
        flex-direction: row;
    }
}

.details-zone {
    display: flex;
    flex-direction: row;
    align-self: stretch;

    &:first-child {
        margin-bottom: 1.7rem;

        @include bp(tab-up) {
            margin-right: 4.8rem;
        }
    }
}

.details-zone__text,
.details-zone__action {
    @extend %secondary-font-normal;
    font-size: 1.5rem;
    color: map-get($black, secondary);
    line-height: 2.6rem;
}

.details-zone__action {
    color: map-get($color, hyperLink);
    text-decoration: underline;
    transition: color 200ms linear;

    &:hover {
        transition: color 200ms linear;
        color: map-get($color, hyperLinkHover);

    }
}

.details-zone__icon {
    font-size: 2.2rem;
    color: map-get($black, secondary);
    margin-right: 0.9rem;
}