@import "../../../components/miyagi-core/styles/variables";
@import "../../../components/miyagi-core/styles/mixins";
@import "../../../components/miyagi-core/styles/typography";

@import "../../../components/accordion/styles/index";
@import "../../../components/miyagi-core/styles/subpage";

.contact-form__select.md-default-theme .md-select-label.md-placeholder {
    color: rgba(0,0,0,0.26);
    width: 100%;
}

.contact-form__select {
    padding-bottom: 0;
    margin-top: 0;
    padding-top: 1.7rem;
    width: 100%;
}

.contact-form__select.md-default-theme .md-select-label {
    width: 100%;
}

.contact-form__select.md-default-theme .md-select-label span:first-child {
    flex: none;
}

.contact-form__input-ctn {
    margin-top: 0.6rem;

    &:first-of-type {
        margin-top: 0;
    }
}

.contact-card {
    /*padding: 1.6rem map-get($keyline, small) 4.5rem;*/
    padding: 1.6rem 2rem 4.5rem;
    margin: 0;

    @include bp(tab-up) {
        /*padding: map-get($keyline, large);*/
        padding: 3rem;
    }

    md-input-container {
        padding-top: 2.3rem;
    }

    md-toast {
        left: 50%;
        margin: 0px auto;
    }
}

p.contact-card__intro {
    width: 100%;
    max-width: 62.8rem;
    font-size: 1.8rem;
    line-height: 2.8rem;

    @include bp(tab-up) {
        font-size: 1.8rem;
    }
}

.contact__subpage-header__content {
    max-width: 82.5rem;
}

.contact-form__button-ctn {
    margin-top: 2.4rem;

    @include bp(tab-up) {
        margin-top: 13.3rem;
    }

    .md-button.md-primary:not([disabled]) {
        color: palette(sanMarino, base);
    }
}

.contact-form__loader {
    top: -1rem;
}

.contact-error {
    width: 100%;
}

md-backdrop.md-select-backdrop {
    z-index: map-get($z-index, select-backdrop);
}

.contact-toast--success {
    background-color: palette(caribbeanGreen, base);
}

.contact-toast--error {
    background-color: palette(pomegranate, base);
}

md-input-container:not(.md-input-invalid).md-input-focused.md-accent label {
    color: palette(cerulean, base);
}

md-input-container:not(.md-input-invalid).md-input-focused.md-accent .md-input {
    border-color: palette(cerulean, base);
}